*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  min-height: 100vh;
  /* background-colo: rgb(235, 188, 34); */
  /* background-image: linear-gradient( 68.6deg,  rgba(79,183,131,1) 14.4%, rgba(254,235,151,1) 92.7% );   */
  /* background-color: #0093E9;
  background-image: linear-gradient(0deg, #0093E9 0%, #80D0C7 100%); */
  /* background-color: #4158D0; */
  background-image: linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);  
  /* display: flex; */
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
}

.heading {
  font-size: 50px;
  color: white;
  /* font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
  font-family: "Bebas Neue", sans-serif;
  /* max-width: 700px; */
  /* width: 100%; */
  border: 3px solid;
  border-radius: 1rem;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 10px;
  
}
.headDiv{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.score {
  height: 20vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: "Oswald", sans-serif;
}

.choice {
  height: 25vh;
  display: flex;
  justify-content: center;
  font-family: "Oswald", sans-serif;
}

.computer-hand {
  width: 250px;
  height: 150px;
}

.user-hand {
  width: 250px;
  transform: rotateY(180deg);
  height: 150px;
}

.button-div {
  display: flex;
  justify-content: center;
  padding: 20px;
  margin-top: -37px;
  font-family: "Oswald", sans-serif;
  gap: 5px;
  align-items: center;
  cursor: pointer;
}

.button {
  width: 150px;
  height: 50px;
  justify-content: space-around;
  align-items: center;
  /* background-color: rgb(128, 128, 128); */
  color: white;
background-image: linear-gradient(90deg, #eb527d 0%, #f1e95f 46%, #56a2ff 100%);
/* background: #659999;  /* fallback for old browsers */
/* background: -webkit-linear-gradient(to right, #f4791f, #659999);  /* Chrome 10-25, Safari 5.1-6 */
/* background: linear-gradient(to right, #f4791f, #659999); W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
  margin: 20px;
  font-family: "Oswald", sans-serif;
  
}

.result {
  padding: 10px;
  margin-top: -20px;
  color: white;
  font-family: "Oswald", sans-serif;
  /* border: 3px solid;
  border-radius: 1rem; */
  justify-content: center;
  align-items: center;
}

/* Media Queries for Responsive Layout */
@media (max-width: 600px) {
  .score {
    flex-direction: column;
    align-items: center;
  }

  .choice {
    flex-direction: column;
  }

  .user-hand,
  .computer-hand {
    width: 80px;
    height: 80px;
  }

  .button-div {
    flex-direction: column;
    gap: 5px;
  }

  .button {
    padding: 8px 16px;
  }
}